<template>
  <view>
    <!-- 3D 模型区域 -->
    <canvas id="container" type="webgl">
      <model />
    </canvas>
 
    <!-- 卡片区域 -->
    <scroll-view class="card-container">
      <view class="card-row">
        <view class="card">
			<p >当前电量</p>
          <div class="text_my">85%</div>
          <image style="width: 50px;height: 50px;" src="../static/dian.png" class="battery-icon"></image>
        </view>
        <view class="card">
        <p>续航里程</p>
        <div class="text_my">355KM</div>
          <image style="width: 50px;height: 50px;" src="../static/xvhang.png" class="car-icon"></image>
        </view>
      </view>
      <view class="card-row"> 
        <view class="card">
        	<p>胎压状况</p>
          <div class="text_my">正常</div>
          
          <image style="width: 50px;height: 50px;" src="../static/chelun.png" class="battery-icon"></image>
        </view>
        <view class="card">
        	<p>型号</p>
          <div class="text_my">BYD</div>
          
          <image style="width: 50px;height: 50px;" src="../static/car.png" class="battery-icon"></image>
        </view>
      </view>
    </scroll-view>

    <u-tabbar :value="value6" @change="name => value6 = name" :fixed="true" :placeholder="true"
      :safeAreaInsetBottom="true">
      <u-tabbar-item text="首页" icon="home" @click="gotoNav(0)"></u-tabbar-item>
      <u-tabbar-item text="服务" icon="photo" @click="gotoNav(1)"></u-tabbar-item>
      <u-tabbar-item text="小慧同学" @click="audioCHangeWord()">
        <image class="u-page__item__slot-icon" slot="active-icon" src="https://liujiangaidajiao.oss-cn-beijing.aliyuncs.com/2023/12/29/460747a012f847dca9ab63bc70c0232ea65e54f7223f91cba72166703fb454f4eeea17d4.jpg"
          style="width: 30px;height: 30px;"></image>
      </u-tabbar-item>
      <u-tabbar-item text="数据" icon="play-right" @click="gotoNav(2)"></u-tabbar-item>
      <u-tabbar-item text="我的" icon="account" @click="gotoNav(3)"></u-tabbar-item>
    </u-tabbar>
  </view>
</template>

<script setup>
  import model from "./index/model.vue"
  export default {
    data() {
      return {
        value6: 0,
        right2: false,
        right1: false,
        left1: false,
        left2: false,
		 responseData: '',
		isListening: false // 判断是否在语音监听中
      };
    },
    mounted() {

    },
    components: {
      model: model
    },
    methods: {
      gotoNav(index) {
		  uni.navigateTo({
		  	url:'/pages/service'
		  })
      },
	  audioCHangeWord() {
	    var that = this;
	    // 创建 SpeechRecognition 对象
	    // eslint-disable-next-line no-undef
	    var recognition = new webkitSpeechRecognition();
	    if (!recognition) {
	      // eslint-disable-next-line no-undef
	      recognition = new SpeechRecognition();
	    }
	    // 设置语言
	    recognition.lang = "zh-CN";
	    // 开始语音识别
	    recognition.start();
	    that.isListening = true;
	    // 监听识别结果
	    recognition.onresult = function (event) {
	      var result = event.results[0][0].transcript;
	      console.log("识别的话：", result); // 在这里打印识别的话
		  that.chat(result)
	    };
	    // 监听错误事件
	    recognition.onerror = function (event) {
	      that.isListening = false;
	      that.$message("监听语音失败:" + event.error);
	      console.error(event.error);
	    };
	    // 监听结束事件（包括识别成功、识别错误和用户停止）
	    recognition.onend = function () {
	      that.isListening = false;
	      console.log("语音识别停止");
	    };
	  },
      chat(text) {
		        const url = 'https://ark.cn-beijing.volces.com/api/v3/bots/chat/completions';
		        const headers = {
		          'Content-Type': 'application/json',
		          'Authorization': 'Bearer c31db783-e7ac-42a1-97a1-e41559c011a0',
		        };
		        const body = {
		          "model": "bot-20241012171533-7wdc5",
		          "messages": [
		            {
		              "role": "user",
		              "content": text
		            }
		          ]
		        };
		        uni.request({
		          url: url,
		          method: 'POST',
		          header: headers,
		          data: body,
		          success: (res) => {
		            if (res.statusCode === 200) {
		              this.setData({
		                responseData: res.data,
		              });
					   console.log(res.data); // 打印返回值
		            } else {
		              uni.showToast({
		                title: '请求失败',
		                icon: 'none',
		              });
		            }
		          },
		          fail: (err) => {
		            uni.showToast({
		              title: '网络错误',
		              icon: 'none',
		            });
		            console.error(err);
		          },
		        });
		}
    }
  };
</script>

